.slide_bullet {
  list-style: none;
  position: fixed;
  right: 5px;
  top: 50%;
  transform: translate(-50%, 0px);
  z-index: 10;
}

.slide_bullet li {
  display: block;
  width: 9px;
  height: 9px;
  @endif {}  border-color: #777;
  border-radius: 50%;
  margin-bottom: 12px;
  cursor: pointer;
  transition: 0.5s;
  position: relative;
}

.slide_bullet li:after {
  content: "";
  display: block;
  border-radius: 50%;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 6px;
  height: 6px;
  @endif {}  background-color: #3e3e3e;
  transition-duration: 0.5s;
}

.slide_bullet li:hover {
  border-color: #3FBFBB;
}

.slide_bullet li:hover:after {
  background-color: #3FBFBB;
  width: 12px;
  height: 12px;
  @endif {}
}

.slide_bullet li.active {
  background-color: #3FBFBB;
}

.slide_bullet li.active:after {
  background-color: #3FBFBB;
  width: 12px;
  height: 12px;
  @endif {}
}

