}

.section_solution .solution_box .box_inner:after, .section_solution .solution_box .box_inner:before {
  content: "";
  display: block;
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.section_solution .solution_box .box_inner:after {
  background-color: rgba(0, 0, 0, 0.4);
  transition: 0.5s;
}

