  color: #fff;
  transition: 0.5s;
  font-weight: 300;
  background-size: cover;
}

.section_solution .solution_box .box_inner h4.solution_title:after {
  content: "";
  display: block;
  width: 72px;
  margin-top: 48px;
  transition: 0.5s;
  border-bottom: solid 2px #fff;
  margin-left: auto;
  margin-right: auto;
}

.section_solution .solution_box .btn {
